import React, { useState,useEffect } from 'react'
import {  FaBars, FaCalendarAlt, FaMeteor, FaSignInAlt, FaTimes, FaUserShield,FaFilm, FaNewspaper,FaStore, FaSignOutAlt, FaHome } from 'react-icons/fa';
import {Link} from 'react-router-dom'
import {useGlobalContext} from '../context'
const Navbar = () => {
    const {dangnhap,setDangnhap}=useGlobalContext();
    const [nickname,setNickname]=useState(null);
    const [loading,setLoading]=useState(true)
    useEffect(()=>
        
    {
        if(dangnhap.length===0)
            {

            }
        else
            {
               dangnhap.map((item)=>
                {
                    setNickname(item.tendk)
                })
            }
    })
 
    return (
        <div className="nav">
            <div className="nav-logo">
                <FaMeteor></FaMeteor>
            </div>
            <div className="nav-list">
                <ul>
                <li >
                        <Link to="/"><a href="#lichchieu">Trang trủ</a></Link>
                    </li>
                    <li >
                        <a href="#lichchieu">Lịch chiếu</a>
                    </li>
                    <li >
                       <a href="#cumrap">Cụm rạp</a>
                    </li>
                    <li >
                         <a href="#tintuc">Tin tức</a>
                    </li>
                    <li >
                       <a href="#ungdung">Ứng dụng</a>
                    </li>
                   
                </ul>
            </div>
            <div className="nav-login">
                    
                <pre className={`${dangnhap.length>0 ?"anten":"hienten"}`}><FaUserShield ></FaUserShield>  <Link to="/dangnhap">Đăng nhập  </Link>|  <Link to="/dangky">Đăng ký</Link></pre>
                <pre className={`${dangnhap.length>0 ?"hienten":"anten"}`} onClick={()=>setDangnhap([])}><Link to="/">Đăng xuất  </Link>  <FaSignOutAlt></FaSignOutAlt></pre>
                <p className={`${dangnhap.length>0 ?"hienten":"anten"}`}>Nickname:{nickname}</p>
            </div>

            <label htmlFor="open-sidebar" className="nav-bars">
                <FaBars></FaBars>
            </label>
          
            <input type="checkbox" id="open-sidebar">
            </input>
            <div className="nav-truottrai">
               <label htmlFor="open-sidebar" className="nav-times"><FaTimes></FaTimes></label>
                <ul>
                    <li className={`${dangnhap.length>0 ?"anten":"hienten"}`}>
                        
                        <Link to="/dangnhap">
                            <i ><FaUserShield></FaUserShield></i>
                            Đăng nhập</Link>
                    </li>
                    <li className={`${dangnhap.length>0 ?"hienten":"anten"}`}>
                        
                        
                            <i ><FaUserShield></FaUserShield></i>
                            <a href="#">{nickname}</a>
                    </li>
                    <li className={`${dangnhap.length>0 ?"anten":"hienten"}`}>
                        <Link to="/dangky">
                        <i ><FaSignInAlt></FaSignInAlt></i>
                            Đăng Ký</Link>
                    </li>
                    <li>
                        <Link to="/">
                            <i><FaHome></FaHome></i>
                            Trang trủ
                        </Link>
                    </li>
                    <li >
                        
                        <i><FaCalendarAlt></FaCalendarAlt></i>
                            <a href="#lichchieu">Lịch chiếu</a>
                       
                       
                    </li>
                    <li >
                          
                        
                        <i><FaFilm></FaFilm></i>
                            <a href="#cumrap">Cụm rạp</a>
                    </li>
                    <li >
                    
                       
                            <i><FaNewspaper></FaNewspaper></i>
                            <a href="#tintuc">Tin tức</a>
                    </li>
                    <li >
                     
                            <i><FaStore></FaStore></i>
                            <a href="#ungdung">Ứng dụng</a>
                    </li>
                    <li className={`${dangnhap.length>0 ?"hienten":"anten"}`}>
                        
                    <Link to="/">
                    <i ><FaSignOutAlt></FaSignOutAlt></i>
                            <a href="#" onClick={()=>setDangnhap([])}>Đăng Xuất</a>
                    </Link>
                            
                    </li>
                   
                </ul>
               
            </div>
           <label htmlFor="open-sidebar" className="nav-baophu">
              
           </label>
        </div>
    )
}

export default Navbar
