import React, { useEffect, useState } from 'react'
import {useGlobalContext} from '../context'
import { data } from './Silde-data-anh'

import {FaBars, FaChevronDown,FaChevronUp} from 'react-icons/fa'
import { Link } from 'react-router-dom'

const Cumrap = () => {
    const {luudatve,setLuudatve,dangnhap,hienphim}=useGlobalContext()
    const [bienrap,setBienrap]=useState(1)
    const [bienchinhanh,setBienchinhanh]=useState(-1)
    const {rap}=useGlobalContext();
    console.log(hienphim);
console.log(rap);
    const chonrap=(bien)=>
        {
            setBienrap(bien)

        }
    useEffect(()=>
        {
            setBienchinhanh(0)
        },[bienrap])
    useEffect(()=>
        {
            if(window.innerWidth>739)
                {
                    setBienchinhanh(0)
                }
        },[window.resizeBy])
    const chonchinhanh=(bien)=>
        {
            console.log(window);
            setBienchinhanh(bien)
            if(bien===bienchinhanh && window.innerWidth<739)
            {
                setBienchinhanh(-1)
            }
        }
 
    return (<>
    <h1 id="cumrap">Những bộ phim được săn đón nhất tại các rạp</h1>
        <div className="rap">
            <div className="logo-rap">
                {rap.map((item,index)=>
                    {
                        const {logo,lstCumRap,mahom,tenHeThongRap,maHeThongRap}=item;
                            return (
                                <div key={index}>
                                    <img className={`${bienrap===index?"":"modi"}`} src={logo} onClick={()=>chonrap(index)}></img>
                                </div>
                            )
                    })}
            </div>
       
        <div >
            {
                rap.map((item,index)=>
                    {
                        const {logo,lstCumRap,mahom,tenHeThongRap,maHeThongRap}=item;
                        if(index===bienrap)
                            {
                                return(
                                    <div className="chieu-lich" key={index}>
                                        {lstCumRap.map((items,index)=>
                                            {
                                                const {danhSachPhim,diaChi,maCumRap,tenCumRap}=items;

                                                let str=danhSachPhim[0].lstLichChieuTheoPhim[0].ngayChieuGioChieu.slice(0,10);
                                                return (
                                                    <div className="chieu-list" key={index}>
                                                      <div className="list-chieu" onClick={()=>chonchinhanh(index)}>
                                                       
                                                          <p className={`${index===bienchinhanh?"maudo":"hihi"}`}>{tenCumRap}</p>
                                                          <i className={`${index===bienchinhanh?"hienlen":"andi"}`}><FaChevronDown></FaChevronDown></i>
                                                          <i className={`${index===bienchinhanh?"andi":"hienlen"}`}><FaChevronUp></FaChevronUp></i>
                                                      </div>
                                                       <div className={`${index===bienchinhanh ? "gio-chieu chieucao nenmau":"gio-chieu komau"}`}>
                                                           <div className="tt-phimhot">
                                                           <img className="logo-phimhot" src={danhSachPhim[0].hinhAnh}></img>
                                                           <div className="maphim-hot">
                                                               {danhSachPhim[0].maPhim}
                                                           </div>
                                                           <p>{danhSachPhim[0].tenPhim}</p>
                                                           </div>
                                                          

                                                           {danhSachPhim[0].lstLichChieuTheoPhim.map((itemss,index)=>
                                                            {
                                                                if(itemss.ngayChieuGioChieu.slice(0,10)===str)
                                                                    {
                                                                       
                                                                        return(
                                                                         <Link to={`${dangnhap.length>0?"/datghe":"/dangnhap"}`}>
                                                                          <div className="gio-cuthe" key={index} onClick={()=>
                                                                                {
                                                                                    setLuudatve([danhSachPhim[0].maPhim,danhSachPhim[0].tenPhim,tenCumRap,danhSachPhim[0].hinhAnh,itemss.giaVe,itemss.ngayChieuGioChieu])
                                                                                }}>
                                                                                <p>{itemss.ngayChieuGioChieu.slice(10)}</p>
                                                                            </div>
                                                                         </Link>  
                                                                        )
                                                                    }
                                                            })}
                                                        </div>
                                                        </div>
                                                )
                                            })}
                                        </div>
                                )
                            }
                    })
            }
        </div>
        </div>
        </>
    )
}

export default Cumrap
