import React, { useEffect, useState } from 'react'
import {data} from './Silde-data-anh'
import { FiChevronRight, FiChevronLeft } from 'react-icons/fi';
const Slide = () => {
    const [picture,setPicture]=useState(data)
    const [index,setIndex]=useState(0);
    const [btnk,setBtnk]=useState(false);
    const [btn1,setBtn1]=useState(false);
    const [btn2,setBtn2]=useState(false);
    const [btn3,setBtn3]=useState(false);
    
    
    useEffect(()=>
        {
            const lastIndex=picture.length-1;
            if(index==0)
            {
                setBtnk(true);
            }
        else
            {
                setBtnk(false);
            }
            if(index==1)
            {
                setBtn1(true);
            }
        else
            {
                setBtn1(false);
            }
            if(index==2)
            {
                setBtn2(true);
            }
        else
            {
                setBtn2(false);
            }
            if(index==3)
            {
                setBtn3(true);
            }
        else
            {
                setBtn3(false);
            }
           
            if(index<0)
                {
                    setIndex(lastIndex)
                }
            if(index>lastIndex){
                setIndex(0)
            }
          
        },[index,picture])
    useEffect(()=>
{
    let slider = setInterval(() => {
        setIndex(index + 1);
      }, 5000);
      return () => {
        clearInterval(slider);
      };
    },[index])
    
    return (
      <section className="ssection">
         <div className="section-center">
             {picture.map((pic,picIndex)=>
                {
                    const {id,image,name,title,qoute}=pic;
                    let position='nextSlide';
                    if(picIndex===index)
                        {
                            position="activeSlide";
                        }
                    if(picIndex===index-1 || (index === 0 && picIndex === picture.length - 1))
                        {
                            position="lastSlide";
                        }
                        return(
                            
                            <article className={position}  key={id}>
                                <img src={image} alt={name} className="person-img"></img>
                            </article>
                        )
                })}
               <FiChevronLeft className="prev" onClick={()=>setIndex(index-1)}></FiChevronLeft>
               <FiChevronRight className="next" onClick={()=>setIndex(index+1)}></FiChevronRight>
                
                <div className="input-radio">
                    <input type="radio" name="radio" id="radio1" onClick={()=>setIndex(0)} checked={btnk}></input>
                    <input type="radio" name="radio" id="radio2" onClick={()=>setIndex(1)} checked={btn1}></input>
                    <input type="radio" name="radio" id="radio3" onClick={()=>setIndex(2)} checked={btn2}></input>
                    <input type="radio" name="radio" id="radio4" onClick={()=>setIndex(3)} checked={btn3}></input>
                  

                </div>
         </div>
        
      </section>
    )
}

export default Slide
