import React, { useState } from 'react'
import {useGlobalContext} from '../context'
import {FaPlayCircle, FaVideo,FaStar, FaChevronLeft, FaChevronRight,FaTimes} from 'react-icons/fa'
import {Link} from 'react-router-dom'
const Listphim = () => {
    const {hienphim,trangthai,handleClick2,handleClick1}=useGlobalContext();
    const [bien,setBien]=useState(0)
    const [movideo,setMovideo]=useState(false)
    const [link,setLink]=useState(null)
    const handleCLickphai=()=>
        {
            if(bien===24)
            {
               setBien(0)
            }
            else
            {
                setBien(bien+8)
            }
        }
     const handleCLicktrai=()=>
        {
            
            if(bien===0)
            {
               setBien(24)
            }
            else
            {
                setBien(bien-8)
            }
        }
    const handleMovideo=(link)=>{
        setMovideo(true)
        setLink(link)
    }
    const handClodevideo=()=>
        {
            setMovideo(false)
            setLink()
        }
   
    return (
        <div className="list" draggable="true" onDragEndCapture={handleCLicktrai} >
            <div className={`${movideo ? "list-video video-hien" :"list-video"}`}>
                <div className="khung-video">
                    <iframe width="80%" height="350px" src={link} allowFullScreen></iframe>
                </div>
                <i><FaTimes onClick={handClodevideo}></FaTimes></i>
            </div>
           <div className="list-button" id="lichchieu">
               <button type="button" 
               className={`${trangthai ? "list-button-dangchieu mau":"list-button-dangchieu"}`}
                onClick={handleClick1}>Đang chiếu</button>
               <button type="button" 
               className={`${trangthai ? "list-button-dangchieu ":"list-button-dangchieu mau"}`} 
               onClick={handleClick2}>Sắp chiếu</button>
            </div> 
            <div className="list-tp">
                <button type="button" className="trai" onClick={handleCLicktrai}><FaChevronLeft></FaChevronLeft></button>
                <button type="button" className="phai" onClick={handleCLickphai}><FaChevronRight></FaChevronRight></button>
            </div>
            <div className="list-phim">
                <div className="grid">
                <div className="row">
                    {
                        hienphim.map((item,index)=>
                            {
                            const {maPhim,hinhAnh,trailer,tenPhim,moTa,danhGia,ngayKhoiChieu}=item;
                                if(index>=bien && index<bien+8 )
                                    {
                                        return (
                                            <div className="col l-3 m-6 c-12 codinh" key={index}>
                                                <img src={hinhAnh}></img>
                                                <div className="sao">
                                                        <p>{danhGia}</p>
                                                        <i><FaStar></FaStar></i>
                                                    </div>
                                                <div className="list-an">
                                                    
                                                    <i><FaPlayCircle onClick={()=>handleMovideo(trailer)}></FaPlayCircle></i>
                                                    </div>
                                                <div className="list-tt">
                                                    <div className="maphim">
                                                       {maPhim}
                                                        </div>
                                                    <p>{tenPhim}</p>
                                                    
                                            </div>
                                             <Link to={`phim/${maPhim}`}> <div className="muave">Mua vé</div></Link>  
                                            </div>
                                        )
                                    }
                            })
                    }
                </div>
            </div>
        </div>
        </div>
    )
}

export default Listphim
