import React from 'react'
import { Link } from 'react-router-dom';
import {useGlobalContext} from '../context'
const CumRap = ({luarap,bien,luachonngay}) => {
     let sl=0;
     const {setLuudatve,dangnhap}=useGlobalContext();

     
    return (
        <div className="thongtin-chinhanh">
              {luarap.map((item,index)=>
          {
            const {danhSachPhim,diaChi,maCumRap,tenCumRap}=item;
                    let t=0
                    danhSachPhim.map((item)=>
                        {const arr=item.lstLichChieuTheoPhim.map((items,index)=>parseInt(items.ngayChieuGioChieu.slice(8,10)))
                            
                            if(item.maPhim===bien && arr.includes(luachonngay))
                                {
                                    t++;     
                                }
                            
                           
                        })
                        if(t==0)
                        {
                           sl++;
                           if(sl!==luarap.length)
                            {return (
                                <div key={index}></div> 
                            )}
                            return(
                                <h3>Không có lịch chiếu hôm nay</h3>
                            )
                        }    
                        return(
                
                            <div key={index} className="thongtin-giochieu">
                                 {tenCumRap} ({diaChi})
                                    {danhSachPhim.map((item,index)=>
                                        { const {hinhAnh,lstLichChieuTheoPhim,maPhim,tenPhim}=item;
                                            if(maPhim===bien)
                                                {
                                                    return(
                                                        <div key={index} >
                                                            {
                                                                lstLichChieuTheoPhim.map((items,index)=>
                                                                    {
                                                                        if(parseInt(items.ngayChieuGioChieu.slice(8,10))===luachonngay)
                                                                            {
                                                                                return (
                                                                                  <Link to={`${dangnhap.length===0?"/dangnhap":"/datghe"}`}>
                                                                                      <div className="thongtin-gio" key={index} onClick={()=>
                                                                                        {
                                                                                            setLuudatve([maPhim,tenPhim,tenCumRap,hinhAnh,items.giaVe,items.ngayChieuGioChieu])
                                                                                        }}>
                                                                                        {items.ngayChieuGioChieu.slice(10)}
                                                                                    </div>
                                                                                  </Link>  
                                                                                )
                                                                            }
                                                                    })
                                                            }
                                                       </div >
                                                    )
                                                }
                                            
                                                   
                                                
                                        })}
                                </div>
                            )
                        
                    })}       
        </div>
    )
}

export default CumRap
