import React, { useEffect, useContext,useState } from 'react'
const AppContext=React.createContext();
const AppProvider=({children})=>
    {
        const url="https://movie0706.cybersoft.edu.vn/api/QuanLyPhim/LayDanhSachPhim?maNhom=GP01"
        const url1="https://movie0706.cybersoft.edu.vn/api/QuanLyRap/LayThongTinLichChieuHeThongRap?maNhom=GP01"

        const [listphim,setListphim]=useState([]);
        const [hienphim,setHienPhim]=useState([]);
        const [rap,setRap]=useState([])
        const [trangthai,setTrangthai]=useState(true);
        const [luudanhgia,setLuudanhgia]=useState(JSON.parse(localStorage.getItem("followme"))||[])
        const [dangnhap,setDangnhap]=useState([]);
        const [dangky,setDangky]=useState(JSON.parse(localStorage.getItem("luulist"))||[]);
        const [listcomment,setListcomment]=useState(JSON.parse(localStorage.getItem("binhluan"))||[])
        const [bient,setBient]=useState(0);
        const [luudatve,setLuudatve]=useState([]);
        const [luuchonghe,setLuuchonghe]=useState(JSON.parse(localStorage.getItem("nhudb"))||[])
        const [loading,setLoading]=useState(true)

        const handleClick1=()=>
        {
            setTrangthai(true)
            setHienPhim(listphim.filter((item,index)=>index<32))

        }
    const handleClick2=()=>
        {
            setTrangthai(false)
            setHienPhim(listphim.filter((item,index)=>index>32))
        }

        useEffect(async()=>
            {
                const responsive=await fetch(url)
                const data=await responsive.json()
                setListphim(data)
               setHienPhim(data.filter((item,index)=>index<32))
              
                setTimeout(()=>
                    {
                        setLoading(false)
                    },2000)
            },[])
         useEffect(async()=>
            {
                const responsive=await fetch(url1)
                const data=await responsive.json()
               setRap(data)
              

            },[])
        return <AppContext.Provider value={{
            hienphim,
            rap,
            handleClick1,
            handleClick2,
            trangthai,
            luudanhgia,
            setLuudanhgia,
            dangky,setDangky,
            dangnhap,setDangnhap,
            listcomment,setListcomment,
            bient,setBient,
            luudatve,setLuudatve,
            luuchonghe,setLuuchonghe,
            loading
          
        }}>
            {children}
        </AppContext.Provider>
    }
    export const useGlobalContext = () => {
        return useContext(AppContext)
      }
      
     export { AppContext, AppProvider }