import React, { useState,useEffect } from 'react'
import { FaGrinTears } from 'react-icons/fa'
import {Link} from 'react-router-dom'
import {useGlobalContext} from '../context'
import Loading from '../loading/Loading'
const Dangnhap = () => {
    const {dangky,dangnhap,setDangnhap,luudatve}=useGlobalContext();
    const [taikhoandn,setTaikhoandn]=useState("");
    const [taikhoanmk,setTaikhoanmatkhau]=useState("")
    const [newArr,setNewArr]=useState([])
    const [loading,setLoading]=useState(true)
    const handledangnhap=()=>
        {
            
            if(newArr.length===0)
                {
                    alert("Tên tài khoản hoặc mật khẩu không chính xác")
                }
            else{
                setDangnhap(newArr);
                    alert("Bạn đã đăng ký thành công")
            }
        }
    useEffect(()=>
        {
            setTimeout(()=>
            {
                setLoading(false)
            },2000)
        },[])
        useEffect(()=>
        {
            setNewArr(dangky.filter((item)=>item.tendk===taikhoandn && item.mk===taikhoanmk))
            
        },[taikhoandn,taikhoanmk])  
    if(loading)
        {
            return(
                <Loading></Loading>
            )
        }
    return (
        <div className="dangnhap">
            <div className="khungmo">
            <form className="grid">
            <div className="dangky-tt">
                   <i><FaGrinTears></FaGrinTears></i>
                    <h3>Đăng Nhập</h3>
                </div>
            <div className="nhap-vo">
                <div className="row">
                    <input 
                    type="text"
                    className="col l-o-1 l-10 m-o-1 m-10 c-o-1 c-10"
                    placeholder="Tên Đăng Nhập"
                    onChange={(e)=>setTaikhoandn(e.target.value)}>
                    
                    </input>
                    <input 
                    type="password"
                    className="col l-o-1 l-10 m-o-1 m-10 c-o-1 c-10"
                    placeholder="Mật Khẩu"
                    onChange={(e)=>setTaikhoanmatkhau(e.target.value)}>
                    
                    </input>
                    <div className="uudai1 col l-o-1 l-10 m-o-1 m-10 c-o-1 c-10">
                            
                        <input type="checkbox" ></input>
                            <p>Nhớ mật khẩu</p>
                            </div>
                </div>
                <Link to={`${newArr.length>0 && luudatve.length>0?"/datghe":`${newArr.length>0 ?"/":"/dangnhap"}`}`}>
                <div className="btn-dangky clo l-o-1 l-10 m-o-1 m-10 c-o-1 c-10" onClick={handledangnhap} >
                           Đăng Nhập 
                        </div>
                </Link>
                        <p className="xemxet">
                       
                            <Link to="/dangky">
Chưa có tài khoản? Đăng ký ngay</Link>

                        </p>
            </div>
            </form>
            </div>
        </div>
    )
}

export default Dangnhap
