import React, { useEffect, useState } from 'react'
import { FaLaughWink } from 'react-icons/fa'
import { Link } from 'react-router-dom'
import {useGlobalContext} from '../context'
import Loading from '../loading/Loading'

const Dangky = () => {
    let t=0;
    const {dangky,setDangky}=useGlobalContext();
    const [ho,setHo]=useState("")
    const [ten,setTen]=useState("")
    const [tendn,setTendn]=useState("")
    const [matkhau,setMatkhau]=useState("")
    const [nhaplai,setNhaplai]=useState("")
    const [email,setEmail]=useState("")
    const [sodt,setSodt]=useState("");
    const [loading,setLoading]=useState(true)

console.log(dangky);
    const handleDangky=()=>
        {   
            const arr=dangky.map((item,index)=>item.tendk);

            if(ho==="" || ten==="" || tendn==="" || nhaplai==="" ||matkhau===""
               || email===""||sodt==="")
                {
                    alert("Bạn đã nhập thiếu dữ liêu ! Hãy Nhập lại")
                }
            else if(matkhau!==nhaplai)
                {
                    alert("Bạn đã nhập sai mật khẩu")
                }
            else if(matkhau.length<7)
                {
                    alert("Mật khâu không đủ khỏe ! Hãy đặt lại để bảo mật tốt hơn")
                }
            else if(arr.includes(tendn))
                {
                    alert ("Tên này đã có người dùng ! Hãy đặt lại")
                }
            else{
                const lol=[...dangky,{tendk:tendn,mk:matkhau,sdt:sodt,email:email,ho:ho,ten:ten}]
                setDangky(lol)
                localStorage.setItem("luulist",JSON.stringify(lol))
                t++;
                setTimeout(()=>
                    {
                        alert("Chúc mừng bạn đã đăng ký thành công !!!")
                        setHo("")
        setTen("")
        setTendn("")
        setMatkhau("")
        setNhaplai("")
        setEmail("")
        setSodt("")
                    },500)
            }
        }
        useEffect(()=>
        {
            setTimeout(()=>
            {
                setLoading(false)
            },2000)
        },[])
useEffect(()=>
    {
        setHo("")
        setTen("")
        setTendn("")
        setMatkhau("")
        setNhaplai("")
        setEmail("")
        setSodt("")
        },[t])
if(loading)
        {
            return(
                <Loading></Loading>
            )
        }
    return (
        <div className="dangky ">
           <div className="khungmo">
            <form className="grid">
                <div className="dangky-tt">
                   <i><FaLaughWink></FaLaughWink></i>
                    <h3>Đăng Ký</h3>
                </div>
                <div className="nhap-thongtin">
                    <div className="row">
                        <input type="text" 
                        className="col l-5 m-5 c-o-1 c-10"
                        placeholder="Họ *"
                        value={ho}
                        onChange={(e)=>setHo(e.target.value)}>

                        </input>
                        <input  type="text" 
                        className="col l-4 m-4 c-o-1 c-10"
                        placeholder="Tên *"
                        value={ten}
                        onChange={(e)=>setTen(e.target.value)}>

                        </input>
                        <input type="text" className="col l-o-1 l-10 m-o-1 m-10 c-o-1 c-10"
                        placeholder="Tài Khoản"
                        value={tendn}
                        onChange={(e)=>setTendn(e.target.value)}></input>
                         <input type="password"
                        className="col l-5 m-5 c-o-1 c-10"
                        placeholder="Mật Khẩu *"
                        value={matkhau}
                        onChange={(e)=>setMatkhau(e.target.value)}>

                        </input>
                        <input  type="password" 
                        className="col l-4 m-4 c-o-1 c-10"
                        placeholder="Xác Nhận Mật Khẩu *"
                        value={nhaplai}
                        onChange={(e)=>setNhaplai(e.target.value)}>

                        </input>
                         <input type="text" 
                         className="col l-5 m-5 c-o-1 c-10"
                         placeholder="Email *"
                         value={email}
                         onChange={(e)=>setEmail(e.target.value)}>

                        </input>
                        <input  type="text" 
                        className="col l-4 m-4 c-o-1 c-10"
                        placeholder="Số Điện Thoại"
                        value={sodt}
                        onChange={(e)=>setSodt(e.target.value)}>

                        </input>
                        <div className="uudai col l-o-1 l-10 m-o-1 m-10 c-o-1 c-10">
                            
                        <input type="checkbox" ></input>
                            <p>Nhận các thông báo và ưu đãi mới nhất</p>
                            </div>
                    </div>
                        <div className="btn-dangky clo l-o-1 l-10 m-o-1 m-10 c-o-1 c-10"
                        onClick={handleDangky} >
                            Đăng Ký
                        </div>
                        <p className="xemxet">
                       <Link to="/dangnhap"> Đã có tài khoản tại Tix? Đăng nhập ngay</Link>

                        </p>
                </div>
            </form>
            </div>
        </div>
    )
}

export default Dangky
