import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import {useGlobalContext} from '../context';
const Select = () => {
    const {hienphim,rap,dangnhap,luudatve,setLuudatve}=useGlobalContext();
    const [tenphim,setTenphim]=useState(null);
    const [listcumrap,setListcumrap]=useState([]);
    const [ngaychieu,setNgaychieu]=useState([]);
    const [giochieu,setGiochieu]=useState([])
    const [valueCum,setValueCum]=useState(null);
    const [valueNgay,setValueNgay]=useState(null)
    const [valueGio,setValueGio]=useState(null);
    const [ma,setMa]=useState(null)
    const [gia,setGia]=useState(null)
    const [hinhanh,setHinhanh]=useState(null)
    const [bien,setBien]=useState(null)



    const handClick=()=>
        {
            if(valueGio===null||valueGio==="")
                {
                    alert("Bạn cần phải chọn đầy đủ thông tin")
                }

            else{
                setLuudatve([ma,tenphim,valueCum,hinhanh,gia,bien])
            }
        }
    useEffect(()=>
        {
            
           let arr1=[]
           let arr2=[]
           let arr3=[]
        
           rap.map((item,index)=>
            {
                const {lstCumRap}=item;
            
                lstCumRap.map((items,index)=>
                    {
                        const {danhSachPhim,tenCumRap}=items;
                        const danhgia=danhSachPhim.map((itemss,index)=>itemss.tenPhim)
                        const danhsachloc=danhSachPhim.filter((itemsss,index)=>itemsss.tenPhim===tenphim && tenCumRap===valueCum )
                        
                       if(danhgia.includes(tenphim))
                            {
                                
                            arr1.push(tenCumRap)
                            }
                        danhsachloc.map((bien,index)=>
                            {
                            
                                const {lstLichChieuTheoPhim,maPhim,hinhAnh}=bien;
                                setMa(maPhim)
                                setHinhanh(hinhAnh)
                                lstLichChieuTheoPhim.map((biens,item)=>
                                    {
                                        const {ngayChieuGioChieu}=biens;
                                        if(!arr2.includes(ngayChieuGioChieu.slice(0,10)))
                                            {
                                                arr2.push(ngayChieuGioChieu.slice(0,10))
                                            }
                                        
                                        
                                    })
                              const  danhsachngay=lstLichChieuTheoPhim.filter((item,index)=>item.ngayChieuGioChieu.slice(0,10)===valueNgay);
                                    
                                    danhsachngay.map((bienss,item)=>
                                        {
                                            setGia(bienss.giaVe)
                                            setBien(bienss.ngayChieuGioChieu)
                                            if(!arr3.includes(bienss.ngayChieuGioChieu.slice(0,10)))
                                            {
                                                arr3.push(bienss.ngayChieuGioChieu.slice(10))
                                            }
                                        })
                            })
                    })
            })
            setListcumrap(arr1)
            setNgaychieu(arr2)
            setGiochieu(arr3)

        },[tenphim,valueCum,valueNgay] )
    return (
        <div className="filter" >
            <div className="grid">
            <div className="row">
            <form className="col l-2-4 m-3">
                <label htmlFor="tenphim"  > </label>
                    <select id="tenphim" name="tenphim" required onChange={(e)=>
                        {setTenphim(e.target.value)}}>
                        
                        <option value="">Chọn Phim</option>
                        {hienphim.map((item,index)=>
                     
                           {
                            const {tenPhim,maPhim}=item;
                               return(
                                   <option key={index} value={tenPhim}>{tenPhim}</option>
                               )
                           })}
                    </select>
               
            </form>
           <form className="col l-2-4 m-3">
                <label htmlFor="rap" ></label>
                    <select id="rap" required onChange={(e)=>setValueCum(e.target.value)}>
                       
                        <option value="">Rạp</option>
                        {listcumrap.map((item,index)=>
                     
                           {
                            
                               return(
                                   <option key={index} value={item}>{item}</option>
                               )
                           })}
                    </select>
                
            </form>
            <form className="col l-2-4 m-3">
                <label htmlFor="ngay" > </label>
                    <select id="ngay" required onChange={(e)=>setValueNgay(e.target.value)}>
                       
                        <option value="">Ngày</option>
                        {ngaychieu.map((item,index)=>
                     
                           {
                            
                               return(
                                   <option key={index} value={item}>{item}</option>
                               )
                           })}
                    </select>
              
            </form>
            <form className="col l-2-4 m-3" onChange={(e)=>setValueGio(e.target.value)}>
                <label htmlFor="gio" > </label>
                    <select id="gio" required>
                       
                        <option value="">Giờ</option>
                        {giochieu.map((item,index)=>
                     
                           {
                            
                               return(
                                   <option key={index} value={item}>{item}</option>
                               )
                           })}
                    </select>
              
            </form>
            
            <form className="col l-2-4 m-12" >
                    <Link to={`${luudatve===0 ? "#" : `${dangnhap.length===0?"/dangnhap":"/datghe"}`}`}>

                        <button type="button" onClick={handClick}>Mua Ngay</button>
                    </Link>
            </form>
            </div>
            </div>
        </div>
    )
}

export default Select
