import React, { useEffect, useState } from 'react'
import { FaCheckCircle, FaDizzy, FaPiedPiperPp, FaTimesCircle } from 'react-icons/fa';
import {useGlobalContext} from '../context'
import {data} from './dataghe'
const Datghe = () => {
  
   const {luudatve,luuchonghe,setLuuchonghe}=useGlobalContext()
 
   const [ghedangchon,setGhedangchon]=useState([])

    let arr=[];
    for(let i=0;i<25;i++)
        {
            const so=Math.floor(Math.random()*50+21)
                {
                    if(!arr.includes(so))
                        {
                            arr.push(so)
                        }
                }

        }


    useEffect(()=>
        {
            let lol=[]
            if(luuchonghe.length===0)
                {
                    lol=[{maPhim:luudatve[0],cumRap:luudatve[2],giochieu:luudatve[5],bang:arr}]
                }
            else{
            const arr1=luuchonghe.map((item,index)=>item.maPhim)
            const arr2=luuchonghe.map((item,index)=>item.cumRap)
             const arr3=luuchonghe.map((item,index)=>item.giochieu)
             if(!arr1.includes(luudatve[0]) || !arr2.includes(luudatve[2]) || !arr3.includes(luudatve[5]))
                {
                    lol=[...luuchonghe,{maPhim:luudatve[0],cumRap:luudatve[2],giochieu:luudatve[5],bang:arr}]

                }
            else
                {
                    lol=luuchonghe
                }
            
                
            }
            setLuuchonghe(lol)
            localStorage.setItem("nhudb",JSON.stringify(lol))
        },[])
const handlethanhtoan=()=>
        {
            const arr=ghedangchon.map((item)=>item.id)
         const lol=luuchonghe.map((item,index)=>
                {
                    if(item.maPhim===luudatve[0] && item.cumRap===luudatve[2] && item.giochieu===luudatve[5])
                        {
                            return {...item,bang:item.bang.concat(arr)}
                        }
                    else{
                        return {...item}
                    }
                })
            console.log(lol);
            setTimeout(()=>
            {
                setLuuchonghe(lol)
            setGhedangchon([])
            localStorage.setItem("nhudb",JSON.stringify(lol))
            alert("Bạn đã mua vé thành công thành công")
            },100)
        }
   const handleChonghe=(id,name)=> {

      
        {
            setGhedangchon([...ghedangchon,{id:id,name:name}])
        }
      
    }
const xoadangchon=(id)=>
    {
        setGhedangchon(ghedangchon.filter((item)=>item.id!==id))
    }
   
    if(luudatve.length===0 || luuchonghe.length===0)
        {
            return(
                <div>

                </div>
            )
        }

    return (
        <div className="datghe ">
            <div className="grid wide">
            <div className="row">
                <div className="col l-9 m-6 c-12">
            <div className="ghe-thongtin">
                <div className="kiemtra-thongtin">
                    <h1>2.Chọn ghế và kiểm tra thông tin</h1>
                    <h3>Phim : <b>{luudatve[1]}</b></h3>
                    <h3>Chi nhánh : <b>{luudatve[2]}</b></h3>
                    <h3>Gia vé : <b>{luudatve[4]} K</b></h3>
                    <h3>Ngày chiếu : <b>{luudatve[5].slice(0,10)}</b></h3>
                    <h3>Giờ chiếu : <b>{luudatve[5].slice(10)}</b></h3>
                </div>
                
                            <div className="sodo-ghe">
                             <div className="luuy-sodo">
                            <p>Hãy đọc xem kỹ trước khi chọn ghế</p>
                            <p>Nên chọn những ghế ở giữa để tiện theo rõi</p>
                            <pre> <i className="green"><FaCheckCircle></FaCheckCircle></i> có thể chọn 
                            <i className="red">  <FaTimesCircle></FaTimesCircle> </i>  đã chọn
                            <i className="vang">  <FaDizzy></FaDizzy></i> đang chọn</pre>
                            <div className="sodo">
                    {
                        luuchonghe.map((item,index)=>
                            {
                            const {maPhim,cumRap,giochieu,bang}=item;
                                if(maPhim===luudatve[0] && cumRap===luudatve[2] && giochieu===luudatve[5])
                                    {
                                        return(
                                            <div key={index}>
                                            
                                                {data.map((items,index)=>
                                                    {
                                                        let arr=ghedangchon.map((item,index)=>item.id)
                                                        if(bang.includes(items.id))
                                                            {
                                                                return(
                                                                    <i key={index} className="red" ><FaTimesCircle></FaTimesCircle></i>
                                                                )
                                                                }
                                                        else if(arr.includes(items.id))
                                                            {
                                                                return(
                                                                    <i key={index} className="vang" onClick={()=>xoadangchon(items.id)}><FaDizzy></FaDizzy></i>
                                                                )
                                                            }
                                                        else{
                                                            return(
                                                                <i key={index} className="green" onClick={()=>handleChonghe(items.id,items.name)}><FaCheckCircle></FaCheckCircle></i>
                                                            )
                                                        }
                                                    })}
                                            </div>
                                        )
                                    }
                            })
                    }
                    </div>
                            </div>
                        </div>
                    </div>
                    </div>
                    
                        <div className="ve-phim col l-3 m-6 c-12">
                            <img src={luudatve[3]}></img>
                            <div className="tt-thanhtoan">
                            <div className="cho-ngoi"><p> Chỗ ngồi :</p>
                                {ghedangchon.map((item)=>
                                    {
                                        return <pre>{item.name}</pre>
                                    })}
                            </div>
                            <p>Số lượng : {ghedangchon.length}</p>
                            <p>Tổng tiền :{ghedangchon.length*luudatve[4]}</p>
                            </div>
                           
                            <button type="button" className="btn-thanhtoan" onClick={handlethanhtoan}>
                                Thanh toán
                            </button>
                        </div>
                        </div>
                        </div>
                    </div>
              
               
                   
           
    )
}

export default Datghe
