
import React from 'react'
import './gird.css'
import './index.css'
import {BrowserRouter as Router,Route,Switch} from 'react-router-dom';
import Navbar from './trangtru/Navbar'
import Trangtru from './trangtru/Trangtru'
import Chitiet from './chitiet/Chitiet'
import Dangky from './login/dangky'
import Dangnhap from './login/dangnhap'
import Datghe from './datghe/Datghe'
import Footer from './trangtru/Footer'
const App = () => {
  return (
 <Router>
   <Navbar></Navbar>
   
   <Switch>
      <Route exact path="/"><Trangtru></Trangtru></Route>
      <Route path="/phim/:maPhim"><Chitiet></Chitiet></Route>
      <Route path="/dangky/"><Dangky></Dangky></Route>
      <Route path="/dangnhap/"><Dangnhap></Dangnhap>
      </Route>
      <Route path="/datghe/"><Datghe></Datghe></Route>
    </Switch>
    <Footer></Footer>
 </Router>
  )
}

export default App
