import React from 'react'

const Thongtin = ({phim}) => {
    return (
        <div className="tt-phim grid">
            <div className="row">
                <div className="col l-2 m-2 c-4">
                    <ul>
                        <li>
                            Tên phim
                           
                        </li>
                        <li>Ngày Công Chiếu</li>
                        <li>Diễn Viên</li>
                        <li>Thể Loại</li>
                        <li>Định dạng</li>
                        <li>Ngôn Ngữ</li>
                    </ul>
                </div>
                <div className="col l-4 m-4 c-8">
                <ul>
                        <li>
                            {phim.tenPhim}
                           
                        </li>
                        <li>{phim.ngayKhoiChieu.slice(0,10)}</li>
                        <li>Fake</li>
                        <li>Hành Động Nhật Bản 2 người</li>
                        <li>3D </li>
                        <li>Việt Nam</li>
                    </ul>
                </div>
                <div className="col l-6 m-6 c-12">
                    <h3>Mô tả</h3>
                    <p>{phim.moTa}</p>
                </div>
            </div>
        </div>
    )
}

export default Thongtin
