import React, { useState } from 'react'
import { FaComment, FaThumbsUp } from 'react-icons/fa'
import {data} from './Tintuc-data'
import {data1} from './data-review'
import {data2} from './data-khuyenmai'
const Tintuc = () => {
    const [biendem,setBiendem]=useState(5)
    const [datahien,setDatahien]=useState(data)
    const thugon=()=>
        {
            if(biendem>5)
            {
            setBiendem(biendem-5);
            }

        }
    const xemthem=()=>
        {
            if(biendem<datahien.length)
                {
                    setBiendem(biendem+5)
                }
        }
    return (
        <div className="tintuc">
            <div className="tintuc-thaydoi">
                <ul >
                    <li onClick={()=>setDatahien(data)} className={`${datahien===data?"maulen":""}`}>Điện ảnh</li>
                    <li onClick={()=>setDatahien(data1)} className={`${datahien===data1?"maulen":""}`}>Review</li>
                    <li onClick={()=>setDatahien(data2)} className={`${datahien===data2?"maulen":""}`}>Khuyến Mãi</li>
                </ul>
            </div >
            <div className="khung-tintuc" id="tintuc">
                <div className="grid">
                    <div className="row">
                        {datahien.map((item,index)=>
                            {if(item.id<=biendem)
                                {
                                if(item.id%5===1 | item.id%5===2)
                                    {
                                        return(
                                            <div className="col l-6 m-6 c-12 loai-1" key={index}>
                                                <img src={item.image} ></img>
                                                <h4>{item.title}</h4>
                                                <p>{item.content}</p>
                                               <i> <FaThumbsUp></FaThumbsUp>  {item.like}</i>
                                              <i> <FaComment></FaComment>  {item.comment}</i>
                                                </div>
                                        )
                                    }
                                if(item.id%5===3 | item.id%5===4)
                                    {
                                        return(
                                            <div className="col l-4 m-6 c-12 loai-2"  key={index}>
                                                <img src={item.image} ></img>
                                                <h4>{item.title}</h4>
                                                <p>{item.content}</p>
                                               <i> <FaThumbsUp></FaThumbsUp>  {item.like}</i>
                                              <i> <FaComment></FaComment>  {item.comment}</i>
                                                </div>
                                        )
                                    }
                                if(item.id%5===0)
                                    {
                                        return(
                                            <div className="col l-4 m-6 c-12"  key={index}>
                                                {
                                                    item.listanh.map((items,index)=>
                                                        {
                                                            return(
                                                                <div className="col l-12"  key={index}>
                                                                    <div className="loai-3">
                                                                  <img src={items.anh1}></img>
                                                                  <p>{items.content}</p>
                                                                  </div>
                                                                    </div>
                                                            )
                                                        })
                                                }
                                                </div>
                                        )
                                    }
                                }
                            })}
                    </div>
                </div>
            </div>
            <div className="nut-nhan">
                <button type="button" className={`${datahien.length>biendem ?"btn-mau":"btn-mau hienlen"}`} onClick={xemthem}>Xem thêm</button>
                <button type="button" className={`${biendem===5 ?"hienlen":""}`} onClick={thugon}>Thu gọn</button>
            </div>
        </div>
    )
}

export default Tintuc
